@import url(https://fonts.googleapis.com/css?family=Handlee);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html, #root {
  height: 100%;
  width: 100%; }

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.content {
  padding: 20px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: "Handlee", cursive; }
  .content .stat {
    width: 20%;
    height: 270px;
    background-color: #000;
    color: #fff;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; }
    .content .stat .value {
      width: 100%;
      font-size: 5em; }
    .content .stat .title {
      width: 100%;
      font-size: 2em; }
  .content .main {
    width: 55%;
    height: 270px; }
    .content .main .header {
      width: 100%;
      height: 122px;
      border-radius: 20px;
      border: 1px solid #000;
      background-color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center; }
      .content .main .header h1 {
        font-size: 5em; }
    .content .main .button-wrapper {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      height: 150px; }
      .content .main .button-wrapper button {
        width: 43%;
        height: 66px;
        border-radius: 20px;
        font-family: "Handlee", cursive;
        font-size: 1.2em;
        border: none;
        box-shadow: 4px 4px 22px 5px #a8a6a8;
        cursor: pointer; }
      .content .main .button-wrapper .download {
        background-color: #000;
        color: #fff; }

@media screen and (max-width: 980px) {
  .content .stat .value {
    font-size: 3em; }
  .content .stat .title {
    font-size: 1em; }
  .content .main .header h1 {
    font-size: 3em; }
  .content .main .button-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    height: 150px;
    flex-wrap: wrap; }
    .content .main .button-wrapper button {
      width: 100%;
      height: 66px;
      border-radius: 20px;
      font-family: "Handlee", cursive;
      font-size: 1.2em;
      border: none;
      box-shadow: 4px 4px 22px 5px #a8a6a8;
      cursor: pointer; }
    .content .main .button-wrapper .download {
      background-color: #000;
      color: #fff; } }

@media screen and (max-width: 680px) {
  .content {
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    font-family: "Handlee", cursive; }
    .content .stat {
      width: 100%;
      height: 200px; }
      .content .stat .value {
        width: 100%;
        font-size: 2.5em; }
      .content .stat .title {
        width: 100%;
        font-size: 1em; }
    .content .main {
      width: 100%;
      height: 270px;
      margin-top: 20px; }
      .content .main .header {
        width: 100%;
        height: 122px; }
        .content .main .header h1 {
          font-size: 2.5em; }
      .content .main .button-wrapper {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        height: 150px; }
        .content .main .button-wrapper button {
          width: 100%; }
        .content .main .button-wrapper .download {
          background-color: #000;
          color: #fff; } }

